body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root{
	width:100%;
	height:100%;
}
.App {
  text-align: center;
  width: 100%;
  height: 100%;
}

.panoramas-list-container{
	position: absolute;
	bottom:0px;
}
.panoramas-list{
	display: -webkit-flex;
	display: flex;
	overflow-x: auto;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
	background-color: rgba(88,88,90,0.7);
	padding: 0px 20px;
}
.panoramas-list-collapse{
	font-size: 30px;
	padding: 10px;
	background-color: rgba(88,88,90,0.7);
	color: white;
}
.panoramas-list-collapse i{
	cursor: pointer;
}
.panoramas-item{
	cursor: pointer;
}
.panoramas-item div{
	width: 160px;
    height: 98px;
    display: -webkit-flex;
    display: flex;
    text-align: center;
    margin: 0px 5px 0px 5px;
    color: white;
    border-width: 5px;
    border-style: solid;
    border-color: white;
    overflow: hidden;
}
.panoramas-item img{
	width:100%;
	z-index: 2;
}
.panoramas-item p{
	color: white;
	text-align: center;
	margin-top:0px;
}
.container{
	width: 100%;
	height: 100%;
	padding-top: 100px;
	background-color: black;
}
